.boddy {
  background-color: #3a3b41;
}
.app__body {
  display: flex;
  background-color: #3a3b41;
  /* height: 100%;
  width: 100%; */
  height: 100vh;
  width: 100vw;
  margin: 0px auto;
  max-width: 2560px;
}

.homescreen {
  background-color: #212529f8;
  border-left: 1px solid rgba(0, 0, 0, 0.08);
  flex: 1 0;
  flex-basis: calc(100% - 250px);
  display: flex;
  /* margin: 0px auto; */
  flex-direction: column;
  overflow-x: auto;
  min-height: 100vh;
  position: relative;
}
/* Hide scrollbar for Chrome, Safari and Opera */
.homescreen::-webkit-scrollbar {
  display: none;
}

/* Hide scrollbar for IE, Edge and Firefox */
.homescreen {
  -ms-overflow-style: none; /* IE and Edge */
  scrollbar-width: none; /* Firefox */
}
.username {
  position: absolute;
  top: 30px;
  right: 60px;
  border-radius: 15px;
  border: solid 1px rgba(255, 255, 255, 0.603);
  color: white;
  padding: 9px 20px;
}
.username h1 {
  font-size: 13px;
}
