.eventcontainer {
  /* background: white; */
  display: flex;
  flex-direction: column;
  padding-bottom: 50px;
}
.tmtitle {
  color: white;
  font-size: 12px;
  padding-bottom: 20px;
  letter-spacing: 1px;
  margin-left: 15px;
}
.tmta {
  max-width: 1000px;
  max-height: 700px;
}
.tmcont {
  display: flex;
  justify-content: space-around;
  height: 300px;
  margin-top: -40px;
  /* margin-bottom: 180px; */
  margin-left: 15px;
}
.bar {
  padding-right: 50px;
}
.tmtable {
  /* background-color: rgb(139, 38, 38); */
  color: white;
  padding-top: 60px;
  margin-left: 100px;
  /* margin-right: 50px; */
  margin-top: 90px;
}
@media only screen and (max-width: 1100px) {
  .tmcont {
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    height: 300px;
    margin-bottom: 110px;
  }
  .tmtitle {
    margin-bottom: 15px;
  }
  .tmta {
    display: flex;
    max-width: 1000px;
    max-height: 400px;
    flex-direction: column;
  }
  .tmtable {
    /* background-color: rgb(139, 38, 38); */
    color: white;
    padding-top: 60px;
    margin-left: 100px;
    margin-right: 150px;
    margin-top: 400px;
  }
}
