.snav {
  display: flex;
  flex-direction: column;
  width: 100%;
  color: white;
  opacity: 1;
  padding-left: 28px;
  padding-top: 30px;
  font-size: 15px;
  font-family: normal;
  font-weight: 600;
  line-height: 20px;
  transition: all 0.5s ease;
  margin-bottom: 250px;
}
.snavitem {
  text-align: start;
  padding: 11px 10px;
  width: 100%;
  display: table;
  cursor: pointer;
}

.nav-links {
  color: #fff;
  display: flex;
  align-items: center;
  text-decoration: none;
  height: 100%;
}
.nav-links:hover {
  color: #A257FF;

}
.snavitem .active {
  color: #A257FF;
}
.logout{
  margin-left: 60px;
  color: white;
  font-size: 18px;
  cursor: pointer;
}
